import React from "react";
import { Link } from "react-router-dom";
import "./headerNav.scss";

export default function HeaderNav() {
  return (
    <header className="header_bottom center sticky">
      <nav className="width mob_tap between">
        <ul className=" between side gap">
          <li>
            <Link to="/rocket-delivery"></Link>
          </li>
          <li>
            <Link to="/rocket-import"></Link>
          </li>
          <li>
            <Link to="/best"></Link>
          </li>
          <li>
            <Link to="/special-offers"></Link>
          </li>
          <li>
            <Link to="/events-coupons">/</Link>
          </li>
        </ul>
        <ul className="between gap pc_ui">
          <li>
            <Link to="/login"></Link>
          </li>
          <li>
            <Link to="/signup"></Link>
          </li>
          <li>
            <Link to="/customer-service"></Link>
          </li>
        </ul>
      </nav>
    </header>
  );
}
