import { createStore } from "redux";

//  
const initialState = {
  isSearchActive: false,
};

//  
const reducer = (state = initialState, action) => {
  switch (action.type) {
    case "TOGGLE_SEARCH":
      return {
        ...state,
        isSearchActive: !state.isSearchActive,
      };
    default:
      return state;
  }
};

//  
const store = createStore(reducer);

export default store;
