import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import "./header.scss";
import logo from "../../assets/img/logo.png";
export default function Header() {
  const searchActive = useSelector((state) => state.isSearchActive);
  return (
    <header className={`center  ${searchActive ? "sticky" : ""} header_top `}>
      <nav className="width wrap side center">
        <span className="material-symbols-rounded pc_ui">menu</span>
        <h1>
          <Link to="/">
            <img src={logo} alt="logo" />
          </Link>
        </h1>
        <input
          id="user-search"
          type="search"
          placeholder="  "
          aria-label="  "
        />
        <div className="button_area pc_ui">
          <span className="material-symbols-outlined" title=" ">
            person
          </span>
          <span className="material-symbols-outlined" title="">
            shopping_cart
          </span>
          <span className="material-symbols-outlined" title=" ">
            list_alt
          </span>
        </div>
      </nav>
    </header>
  );
}
